nav{
    width: 100%;
    height: 64px;
    display:flex;
    align-items:center;
    position:fixed;
    top: 0;
    padding: 5px;
    z-index: 10;
    background-color: white;
    .title{
        display: flex;
        align-items: center;
        margin-right: 1rem;
        p{
            font-size:1.5rem;
            font-weight:600;
            text-transform:uppercase;
            margin: 0;
        }
        img{
            width: 54px;
            height: 54px;
        }
    }
    .search{
        display: flex;
        align-items: center;
        input{
            margin-right: 1rem;
        }
        .fa-search, .fa-caret-right{
            cursor: pointer;
        }
        .fa-caret-right{
            margin-right: .5rem;
        }

    }
    .nav-menu{
        margin: 0;
        margin-left: auto;
        padding: 0;
    }
}